.chat{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px;
    border-bottom: 1px solid whitesmoke;
    cursor: pointer;
}

.chat-avatar{
    height: 35px !important;
    width: 35px !important;
}

.chat:hoover{
    opacity: 0.8;
}

.chat_readIcon{
    color: #ed3b55;
}

.chat_info{
    padding-left: 5px;
    flex:1;
}

.chat_info > p {
    font-size: 9px;
}
